import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CartStatus } from '@core/constants/cart-status';
import { ServiceId } from '@core/constants/service-id.const';
import { ToolsService } from '@core/utils/tools-service';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { BookingCart, BookingSummary } from '@schemas/modules/bookings/booking-summary';
import { DialogService } from '@services/dialog.service';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';
import { BookingDataService } from '@services/modules/bookings/dataservices/booking.data.service';
import { BookingServiceService } from '@services/modules/bookings/services/booking-service.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ScrollService } from '@services/modules/core/scroll-service';
import { UserService } from '@services/modules/user/services/user.service';
import { PlatformService } from '@services/platform.service';
import { ScreenDetectorService } from '@services/screen-detector.service';
import { SettingsService } from '@services/services/settings-service';
import { BehaviorSubject, skip, Subject, takeUntil } from 'rxjs';
// eslint-disable-next-line max-len
import { AccommodationReviewsPopupComponent } from '../../results/accommodation/components/accommodation-reviews-popup/accommodation-reviews-popup.component';

@Component({
    selector: 'tz-accommodation-booking',
    templateUrl: './accommodation-booking.component.html',
    styleUrls: ['./accommodation-booking.component.scss'],
})
export class AccommodationBookingComponent implements OnInit, OnDestroy {
    @ViewChild('detailsContainer', { read: ElementRef, static: false }) detailsContainer!: ElementRef;

    public bookingSummary!: BookingSummary;
    public accommodation: Accommodation | null = null;
    public accommodationReviews: AccommodationReviews | null | undefined;
    public accommodationCartData!: {
        cartData: IAccommodationsCartData;
        paymentCaptureMode: number;
    };
    public loading = false;
    public isMobile$?: BehaviorSubject<boolean>;
    public showScrollBtn = false;
    public scrollBtnRight = 22;

    private bookingId: string;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        public platformService: PlatformService,
        private activeRoute: ActivatedRoute,
        private bookingDataService: BookingDataService,
        private userService: UserService,
        private settingsService: SettingsService,
        private cartService: CartService,
        private accommodationsDataService: AccommodationsDataService,
        private dialogService: DialogService,
        private screenDetectorService: ScreenDetectorService,
        private router: Router,
        private bookingServiceService: BookingServiceService,
        private scrollService: ScrollService
    ) {
        this.bookingId = this.activeRoute.snapshot.params['id'];
        this.isMobile$ = this.screenDetectorService.isMobile;
    }

    ngOnInit() {
        this.initializeBookingAccommodationComponent();
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onCancel(optionsIds: string[]): void {
        const cancelable$ = this.bookingServiceService.checkCancelOptions(optionsIds, this.bookingSummary.cart.items);
        if (!cancelable$) {
            return;
        }

        cancelable$.subscribe((cancelBooking: boolean) => {
            if (!cancelBooking) {
                return;
            }

            const cancelOptions = BookingServiceService.getCartOptions(optionsIds, this.bookingSummary.cart.items);
            for (const option of cancelOptions) {
                option.status = CartStatus.STATUS_CPROGRESS;
            }

            this.formHotelDetails();

            this.bookingDataService.cancelBooking(this.bookingId, optionsIds).subscribe(
                (res) => {
                    this.fetchBookingSummary();
                    this.dialogService.openSnackBar({ message: res.message });
                },
                (failure: any) => {
                    this.fetchBookingSummary();
                    //this.dialogService.openSnackBar({ message: failure.error.message });
                }
            );
        });
    }

    public openReviewsPopup(): void {
        this.dialogService.openAccommodationReviewsDialog(
            AccommodationReviewsPopupComponent,
            this.accommodationReviews
        );
    }

    public gotoBooking(): void {
        this.router.navigate(['/bookings/booking-summary', this.bookingId]);
    }

    public scrollToTop(): void {
        this.scrollService.scrollToTop();
    }

    private formHotelDetails(): void {
        const cartData = ToolsService.deepCopy(this.bookingSummary.cart) as BookingCart;
        const accCartData = this.cartService.getCartData(ServiceId.ACC, cartData);
        if (accCartData) {
            this.accommodation = accCartData.items[0].selectedItem.data;
            this.accommodationCartData = {
                cartData: accCartData,
                paymentCaptureMode: this.bookingSummary.booking.paymentCaptureMode,
            };
        }
    }

    private fetchBookingSummary(): void {
        this.loading = true;
        this.bookingDataService
            .getBooking(this.userService.getCustomerInformation().id, this.bookingId, false)
            .subscribe(
                (booking) => {
                    this.bookingSummary = booking;
                    this.formHotelDetails();

                    if (!this.accommodation) {
                        return;
                    }

                    this.accommodationsDataService
                        .getAccommodationReviews(this.accommodation)
                        .pipe(takeUntil(this.destroy$))
                        .subscribe(
                            (accommodationReviews: AccommodationReviews) => {
                                this.accommodationReviews = accommodationReviews;
                                this.loading = false;
                            },
                            (error) => {
                                console.log('Get booking failure: ', error);
                            }
                        );
                },
                (failure) => {
                    console.log('Get booking failure: ', failure);
                }
            );
    }

    private initializeBookingAccommodationComponent(): void {
        this.fetchBookingSummary();
        this.scrollBtnRight = ScrollService.adjustScrollButtonPosition(this.detailsContainer);

        this.scrollService.onScroll$.pipe(takeUntil(this.destroy$)).subscribe((scrollElement) => {
            this.showScrollBtn = scrollElement.scrollTop >= window.innerHeight * 1.5;
        });

        this.platformService.isMobileDevice$.pipe(takeUntil(this.destroy$)).subscribe(() => {
            requestAnimationFrame(
                () => (this.scrollBtnRight = ScrollService.adjustScrollButtonPosition(this.detailsContainer))
            );
        });

        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.fetchBookingSummary();
        });
    }
}
